<template>
	<view class="content">
		<view class="nav-top">
			<view class="logo-name">
				<view class="logo">
					<image src="../../static/images/home_active.png" class="img"></image>
				</view>
				<view class="name">LucKin Coffee</view>
			</view>
			<view class="index" @click="goindex">先逛一逛</view>
		</view>
		<view class="nav-content">
			<view class="welcome">找回密码！</view>
			<view class="please">Find password</view>
		</view>
		<view class="nav-login">
			<u-form :model="form" ref="uForm">
				<u-form-item label="手机号" prop="number">
					<u-input placeholder='手机号' class="id" v-model="form.number" />
				</u-form-item>
				<u-form-item label="密码" prop="password">
					<u-input placeholder="密码必须为字母开头" v-model="form.password" type="password" />
				</u-form-item>
			</u-form>
			<view class="fogpass" @click="gologin">已有账号，立即登录</view>
			<view class="login" @click="next">提交</view>
			<view class="sign" @click="back">返回</view>
		</view>
	</view>
	</view>
	</view>
</template>

<script>
	import request from "../request/request.js"
	export default {
		data() {
			return {
				showPassword: true,
				show: false,
				form: {
					number: '15915691158',
					password: 'a123456'
				},
				rules: {
					number: [{
						required: true,
						pattern: /^1[3-9]\d{9}$/,
						message: '请输入正确手机号',
						// trigger: ['blur', 'change']
					}],
					password: [{
						required: true,
						pattern: /^[A-Za-z]\w{5,15}$/,
						message: '请输入正确密码格式',
						// trigger: ['blur', 'change']
					}]
				}
			}
		},

		methods: {
			showPwd() {
				this.showPassword = !this.showPassword
			},
			goindex() {
				uni.switchTab({
					url: '../index/index'
				})
			},
			gologin() {
				uni.navigateTo({
					url: '../login/login'
				})
			},
			next() {
				this.$refs.uForm.validate(valid => {
					if (valid) {
						console.log('验证通过');
						request({
							url: "/retrievePassword",
							method: "POST",
							data: {
								phone: this.form.number,
								password: this.form.password
							},
							success: (res) => {
								if (res.data.code == "L004") {
									uni.showToast({
										title: res.data.msg,
										icon: "none",
										mask: true,
										duration: 1000
									})
								} else {
									uni.showToast({
										title: "修改成功，三秒后自动跳转登录页面",
										icon: "none",
										mask: true,
										duration: 3000
									})
									setTimeout(()=>{
										uni.navigateTo({
											url:'../login/login'
										})
									},3000)
								

								}
							}

						})
					} else {
						console.log('验证失败');
					}
				});
			},
			back() {
				uni.navigateBack({
					delta: 1
				})
			}

		},
		onReady() {
			this.$refs.uForm.setRules(this.rules);
		}
	}
</script>

<style lang="less" scoped>
	.content {
		.nav-top {
			margin: 10rpx 30rpx 10rpx 30rpx;
			display: flex;
			justify-content: space-between;
			align-items: center;

			.logo-name {
				display: flex;
				align-items: center;

				.logo {
					.img {
						width: 80rpx;
						height: 80rpx;
					}
				}

				.name {
					margin-left: 20rpx;
					font-weight: bold;
					color: #646566;
				}
			}

			.index {
				color: #0c34ba;
				font-weight: bold;
			}
		}

		.nav-content {
			margin: 30rpx;
			margin-top: 200rpx;

			.welcome {
				font-size: 60rpx;
				font-weight: bold;
				color: #646566;
			}

			.please {
				margin-top: 40rpx;
				font-size: 32rpx;
				color: #83838b;
			}
		}

		.nav-login {
			margin: 30rpx;

			.fogpass {
				margin-top: 50rpx;
				margin-left: 400rpx;
				color: #82848A;
			}

			.login {
				margin: 30rpx;
				margin-top: 100rpx;
				width: 600rpx;
				height: 100rpx;
				background-color: #0c34ba;
				color: #FFFFFF;
				text-align: center;
				line-height: 100rpx;
				border-radius: 50rpx;
			}
		}
	}

	::v-deep .u-form {
		margin: 80rpx 50rpx 30rpx 30rpx;
	}

	::v-deep .u-form-item--left__content__label {
		width: 100rpx;
	}

	::v-deep .uni-input-wrapper {
		margin-left: 110rpx;
	}

	::v-deep .u-form-item__message {
		display: none;
	}

	::v-deep .sign {
		margin: 30rpx;
		margin-top: 50rpx;
		width: 600rpx;
		height: 100rpx;
		text-align: center;
		line-height: 100rpx;
		border-radius: 50rpx;
		border: 2rpx solid #DCDFE6;
	}
</style>
